import React, { useEffect } from "react";
import aboutimage from "../Images/IMG-20191227-WA0023.jpg";
import { Grid } from "@material-ui/core";
import "./About.css";
import Typewriter from "typewriter-effect";
import Aos from "aos";
import "aos/dist/aos.css";
function About() {
  useEffect(() => {
    Aos.init({
      duration: 3000,
      delay: 300,
    });
  }, []);
  return (
    <div id="sec1" className="About_sec">
      <div style={{ fontSize: 40, fontWeight: 900 }}>
        <Typewriter
          options={{
            strings: ["About"],
            cursor: "",
            delay: 400,
            autoStart: true,
            pauseFor: 10000000000,
            loop: true,
          }}
        />
      </div>{" "}
      <div>
        <Grid container xs={12} spacing={1} item>
          <Grid
            container
            item
            lg={6}
            sm={12}
            xs={12}
            direction="row"
            justify="center"
            alignItems="center"
          >
            <img
              data-aos="fade-right"
              src={aboutimage}
              className="img-fluid"
              style={{ height: "50vh" }}
            />
          </Grid>
          <Grid
            item
            container
            lg={6}
            sm={12}
            xs={12}
            style={{ fontSize: 20, textAlign: "justify", lineHeight: "100%" }}
          >
            <p>
              Ea ad occaecat laborum veniam labore excepteur excepteur et
              cupidatat. Ea ut est deserunt reprehenderit deserunt ut enim
              proident exercitation nostrud do commodo. Tempor culpa labore ut
              id. Nostrud adipisicing ullamco sint mollit adipisicing anim.
              Pariatur ea sunt eiusmod esse non do laborum. Nulla elit tempor ex
              anim ut excepteur veniam nostrud do exercitation mollit. Id quis
              sunt consequat proident mollit dolor quis esse ea dolor nulla
              aute. Eiusmod sunt tempor magna qui consectetur dolor do. Do
              mollit aliquip eiusmod excepteur dolore reprehenderit irure. Eu
              enim aliquip pariatur dolor eu. Velit voluptate officia minim
              labore esse in consequat ad. Sunt laborum commodo consequat
              reprehenderit eiusmod veniam in dolor enim consequat cupidatat in.
            </p>
          </Grid>
        </Grid>
      </div>
    </div>
  );
}

export default About;
