import React from "react";
import emailjs from "emailjs-com";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import { Grid, Button } from "@material-ui/core";
import Typewriter from "typewriter-effect";
import "./Contact.css";
function Contact() {
  function sendEmail(e) {
    e.preventDefault();

    emailjs
      .sendForm(
        "gmail",
        "template_yvag63b",
        e.target,
        "user_x3GgbTixvbsPLNYyPT5dJ"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
  }
  return (
    <div className="Contact" id="contact">
      <div style={{ fontSize: 40, fontWeight: 900 }}>
        <Typewriter
          options={{
            strings: ["Contact"],
            cursor: "",
            delay: 400,
            autoStart: true,
            pauseFor: 10000000000,
            loop: true,
          }}
        />
      </div>
      <Grid container direction="row" justify="center" alignItems="center">
        <form className="contact-form" onSubmit={sendEmail}>
          <TextField
            color="secondary"
            className="Input"
            type="text"
            name="contact_number"
            label="Contact Number"
          />
          <br></br>
          <TextField
            color="secondary"
            className="Input"
            type="text"
            name="user_name"
            label="Name"
          />
          <br></br>

          <TextField
            color="secondary"
            className="Input"
            type="email"
            name="user_email"
            label="Email"
          />
          <br></br>

          <TextField
            color="secondary"
            className="Input"
            name="message"
            label="Message"
          />
          <br></br>
          <br></br>
          <Button
            type="submit"
            value="Send"
            variant="contained"
            color="secondary"
          >
            submit
          </Button>
        </form>
      </Grid>
    </div>
  );
}

export default Contact;
